.search_btn {
  position: relative;
  display: inline-block;
}
.search_btn input {
  width: 300px;
  border-radius: 27.5px;
  text-align: start;
  background: rgba(52, 52, 52, 0.1);
  font-family: "Museo Sans Cyrl", sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 145%;
  color: #343434;
  padding: 11px 20px;
  border: none;
}
.search_btn .search_icon {
  position: absolute;
  top: 10px;
  right: 15px;
}

